import React from 'react';
import styled, { css } from 'styled-components';
import { color } from '../../styles/color';
import fontSize from '../../styles/fontSize';

const { normedium } = fontSize;
const { dark, danger } = color;

const Box = styled.div`
  text-align: left;
  padding: 10px;
  font-size: ${normedium};
  border-radius: 3px;
  color: ${dark};

  ${({ color: c }) =>
    c &&
    css`
      color: ${danger};
    `}
`;

const MessageBox = ({ messages, color, children, index = 0 }) => {
  // messages ë°°ì—´ì´ ì •ì˜ë˜ì§€ ì•Šì•˜ì„ ê²½ìš° ë¹ˆ ë°°ì—´ë¡œ ì´ˆê¸°í™”
  messages = messages || [];

  // childrenì´ ìˆìœ¼ë©´ messages ë°°ì—´ì— ì¶”ê°€
  if (children) messages.push(children);

  // ì¸ë±ìŠ¤ë¥¼ í†µí•´ íŠ¹ì • ë©”ì‹œì§€ë§Œ ì‚¬ìš©
  const message = messages[index];

  return message ? (
    <Box color={color}>
      {message}
    </Box>
  ) : null;
};

export default React.memo(MessageBox);
